<template lang="en">
<div class="my-popups">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header theme-primary partition-full  d-flex">
                    <h4 class="modal-title">{{ $t('AvailableContracts') }}</h4>
                    <button type="button" class="close" v-on:click="Close">
                        <em class="fa fa-times" aria-hidden="true"></em>
                    </button>
                </div>
                <div class="modal-body">
                    <dynamic-form lang="en" ref="AvailableContract" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit" :mandatorytext='MandatoryString'></dynamic-form>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import DataService from '../../services/DataService';
export default {
    data() {
        return {
            isLoading: false,
            PageSize: "10",
            PageNumber: "1",
            AvailableContractData: [],
            MandatoryString:'Fields marked with an asterisk (*) are mandatory.',
            FormSchema: [{
                    layoutType: "double",
                    Data: [{
                        astype: "MultiSelectField",
                        label: this.$t('AvailableContracts'),
                        name: "AvailableContracts",
                        value: "",
                        placeholder: this.$t('Select Available Contracts'),
                        validationRules: {
                            "required": true
                        },
                        config: {
                            options: []
                        },
                        visibility: true
                    }]
                }
            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible: true,
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close,
                }
            ]
        }
    },
    created: async function () {
        var vm = this;
        await vm.GetAvailableContracts();
    },
    methods: {
        GetAvailableContracts() {
            var vm = this;
            vm.isLoading = true;
            var url = `pageNum=${vm.PageNumber}&pageSize=${vm.PageSize}&reqFrom=&search=&userId=&screenName=`;
            DataService.GetAvailableContracts(url).then((response) => {
                if(response.data != ''){
                    vm.AvailableContractData = [];
                    vm.AvailableContractData = response.data.result;
                    vm.AvailableContractData.forEach((itm, index) => {
                        (vm.FormSchema)[0].Data[0].config.options.push({
                            name: itm.CHILD_NAME,
                            value: itm.CHILD_USER_ID
                        })
                    })
                }
                vm.$refs.AvailableContract.UpdateKeyValue();
            });
            vm.isLoading = false;
        },
        onSubmit(formfields, val) {
            var vm = this;
        },
        Close: function () {
            this.$parent.isAddContract = false;
        }
    }
}
</script>