<template>
<section>
    <loader :is-visible="isLoading"></loader>
    <tg-list :IsShowAction="true" ModuleName="" SubModuleCode="" :LegendArray="[]"
        :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :widgets="[]"
        IdentityColumn="" :listType="['List']" :ListData="BillerData" :HeaderData="Headers"
        :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
        IsSearchFieldsOnTop="true" :ListDataCallBackFunction="FetchData" :showCheckBox="true"
        :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" :HeaderText="$t('ManageBiller')"
        v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
        v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
    </tg-list>
    <AssignBiller v-if="isAddBiller"></AssignBiller>
</section>
</template>

<script>
import Schema from "../Biller/Schema.js";
import DataService from "../../services/DataService.js";
import AssignBiller from "../Biller/AssignBiller.vue";

export default {
    components: { AssignBiller },
    data() {
        return {
            isLoading: false,
            Headers: [{
                    "COLUMN_NAME": "CLIENT_NAME",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Biller Name',
                    "DISPLAY_ORDER": 1,
                    settings: {
                        isInSlot: false
                    }
                },
                {
                    "COLUMN_NAME": "WORK_CODE",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Contracts',
                    "DISPLAY_ORDER": 2,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "UTILITY_NAME",
                    "DATA_TYPE": "Date",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Created By',
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "STATE_NAME",
                    "DATA_TYPE": "Date",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Created At',
                    "DISPLAY_ORDER": 4,
                    settings: {
                        isInSlot: false
                    }
                }
            ],
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            noRecords: false,
            searchCondition: '',
            BillerData: [],
            listheaderbuttons: Schema.buttons,
            listActions: Schema.listActions,
            leftSearchFields: Schema.leftsearchSchema,
            CompanyId: null,
            UserId: null,
            isAddBiller: false
        }
    },
    created: async function () {
        var vm = this;
        vm.CompanyId = this.GetUserInfo.CompanyId;
        vm.UserId = this.GetUserInfo.ID;
        await vm.FetchData();
    },
    methods: {
        AddBiller(){
            var vm = this;
            vm.isAddBiller = true;
        },
        actionButtonClick: function (callbackfunction, item) {
            switch (callbackfunction) {
                case "Edit":
                    this.EditAddRate();
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
            }
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                if (value.searchConditionJson != '') {
                    const fvalue = value.searchConditionJson.split('=')[1];
                    this.fav = fvalue;
                } else {
                    this.searchCondition = value.searchCondition;
                }
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "AddBiller":
                    this.AddBiller();
                    break;
                case "GroupByContract":
                    this.GroupByContract();
                    break;
            }
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = {
                    ...actionItem
                };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        FetchData: async function () {
            var vm = this;
            //vm.isLoading = true;
            var params = `searchByBiller=&searchByContract=&groupBy=Biller&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&totalRecords=`
            await DataService.GetBillerListing(params).then((response) => {
                if (response.data.result.length > 0) {
                    vm.isLoading = false;
                    vm.BillerData = JSON.parse(response.data.result);
                    vm.TotalRecords = vm.BillerData[0].TOTAL_RECORDS;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;                   
                    vm.noRecords = false;
                } else {
                    vm.BillerData = [];
                    vm.noRecords = true;
                    vm.TotalRecords = 0
                    //vm.isLoading = false;
                }
            })
        },
        commonDeleteData: function (id) {
            var vm = this;
            var val = "";
            vm.isLoading = true;
            if (id == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            } else {
                val = id;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("RateSchedule")), function () {
                    var url = `ids=${val}&tablename=TALYGEN_BE_RATE_SCHEDULE&primaryKey=RATE_SCHEDULE_ID`;
                    DataService.Delete(url).then(function (response) {
                        if (response.status == 200) {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('RateSchedule')]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        } else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('RateSchedule')]), "Success", true, vm.$t("Alert"));
                        }
                    });
                });
            } else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        }      
    }
}
</script>